<template>
  <div class="home">
    <h1>CSS Katas</h1>
    <div id="nav">
      <div v-for="kata in katas" :key="kata.id">
        <router-link
          :to="{ name: 'Kata', params: { id: kata.id, mode: 'mobile' } }"
          >{{ kata.name }}</router-link
        >
      </div>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      katas: [],
    };
  },
  created() {
    fetch("/katas/katas.json")
      .then((res) => res.json())
      .then((data) => (this.katas = data.katas));
  },
};
</script>

<style scoped>
.home {
  padding: 5rem;
}
</style>
